<template>
<div class="register section">
  <div class="container">
    <figure class="image">
      <img src="../assets/fun/earth_downs.jpeg" />
    </figure>
    <div class="form-container content">
      <div class="field">
        <label class="label">Username</label>
        <p class="control">
          <input class="input" type="email" placeholder="Username" v-model="username">
        </p>
      </div>
      <div class="field">
        <label class="label">Email</label>
        <p class="control">
          <input class="input" type="email" placeholder="Email Address" v-model="email">
        </p>
      </div>
      <div class="field">
        <label class="label">Password</label>
        <p class="control">
          <input class="input" type="password" placeholder="Password" v-model="password">
        </p>
      </div>
      <div class="field">
        <label class="label">Select Your Body Type</label>
        <p class="control radios">
          <label class="radio">
              <input type="radio" name="shape" id="small" value="small">
              <div class="radio-box small">
                  <div class="radio-text">
                    <span>Gross</span>
                  </div>
              </div>
            </label>
          <label class="radio">
                <input type="radio" name="shape" id="medium" value="medium">
                <div class="radio-box medium">
                    <div class="radio-text">
                      <span>Normal</span>
                    </div>
                  </div>
            </label>
          <label class="radio">
              <input type="radio" name="shape" id="large" value="large">
              <div class="radio-box large">
                  <div class="radio-text">
                    <span>Gross</span>
                  </div>
              </div>
            </label>
        </p>
      </div>
      <div class="field">
        <p class="control">
          <input class="button button-primary" type="submit" value="Submit" @click="submitRegister" />
        </p>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import {
  mapActions,
  mapGetters
} from 'vuex';

export default {
  name: 'Login',
  data() {
    return {
      username: '',
      email: '',
      password: ''
    }
  },
  computed: {
    ...mapGetters(['isLoggedIn'])
  },
  methods: {
    submitRegister() {
      this.register({
          username: this.username,
          email: this.email,
          password: this.password
        })
        .then(() => {
          if (this.isLoggedIn) {
            this.$router.push({
              path: '/account'
            });
          } else {
            this.$router.push({
              path: '/register'
            })
          }
        })
        .catch(err => console.log(err));
    },
    ...mapActions(['register'])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container {
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
}

.image {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 500px;
  height: auto;
}

@media(min-width: 769px) {
  .container {
    flex-direction: row;
    justify-content: space-around;
  }
  .image {
    width: 300px;
  }
}

@media(min-width: 1024px) {
  .image {
    width: 400px;
  }
}

@media(min-width: 1248px) {
  .image {
    width: 600px;
  }
}

.form-container {
  margin-bottom: 2rem;
}

.radios {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.radio-box {
  width: 128px;
  height: 128px;
  margin-right: 2rem;
  background-color: #71717a;
  transition: 0.2s ease;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.radio-box:active {
  transform: translateY(5px);
}

.radio-text {
  color: #fff;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.radio-text .fa-icon,
.radio-text span {
  display: block;
  font-weight: 700;
  font-size: 1.25rem;
  transition: 0.3s ease;
  user-select: none;
}

input[type="radio"] {
  display: none;
}

input[type="radio"]:checked+.radio-box {
  background-color: #4ca2ad;
  border-color: #4ca2ad;
}

.field:not(:last-child) {
  margin-bottom: 1.4rem;
}

.field:last-child {
  padding-top: 1rem;
}

.radio-box.small {
  background: url(../assets/body_types/small.svg) no-repeat center center;
  background-size: contain;
}

.radio-box.medium {
  background: url(../assets/body_types/medium.svg) no-repeat center center;
  background-size: contain;
}

.radio-box.large {
  background: url(../assets/body_types/large.svg) no-repeat center center;
  background-size: contain;
}

.radio-box.small,
.radio-box.medium,
.radio-box.large {
  --color: #7d7a7a;
  text-shadow: 0px 0px 2px rgba(179, 179, 179, 0.8);
  background-color: var(--color);
  box-shadow: 0px 2px 2px 1px rgba(0, 0, 0, 0.2);
  border: 10px solid var(--color);
}

.radio {
  padding: 1rem 0;
}
</style>



