<template>
<div class="post">
  <div class="post-control">
    <button class="button-success" @click="increment"><icon name="arrow-up" scale="1.5"></icon></button>
    <span class="favorites">{{post.favorites}}</span>
    <button class="button-danger" @click="decrement"><icon name="arrow-down" scale="1.5"></icon></button>
  </div>
  <div class="post-content">
    <h4>{{post.description.charAt(0).toUpperCase() + post.description.slice(1)}}</h4>
    <p>{{post.body}}</p>
    <small><b>{{post.author}}</b> at {{humanReadableDate}}</small>
  </div>
</div>
</template>

<script>
export default {
  name: 'Post',
  props: ['post'],
  methods: {
    increment() {
      this.$store.commit('INCREMENT_POST_FAVORITE_COUNT', {
        post: this.post
      });
    },
    decrement() {
      this.$store.commit('DECREMENT_POST_FAVORITE_COUNT', {
        post: this.post
      });
    }
  },
  computed: {
    humanReadableDate() {
      let date = new Date(this.post.date);
      let format = {
        year: date.getFullYear(),
        month: date.getMonth(),
        day: date.getDate(),
        hours: ('0' + (date.getHours() > 12 ? date.getHours() - 12 : date.getHours())).slice(-2),
        am_pm: date.getHours >= 12 ? 'PM' : 'AM',
        minutes: ('0' + (date.getMinutes())).slice(-2)
      }
      return `${format.hours}:${format.minutes} ${format.am_pm} on ${format.month + 1}/${format.day}/${format.year}`;
    }
  }
}
</script>
<style scoped>
.post {
  background-color: #f7f7f7;
  padding: 1rem;
  display: grid;
  grid-template-columns: max-content 1fr;
  box-shadow: 0px 1px 3px 1px rgba(0, 0, 0, 0.15);
}

.post-control {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 0.5rem;
  margin-right: 1.5rem;
}

.post-control button {
  width: 2.5rem;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.post-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  font-size: 0.75rem;
}

@media(min-width: 769px) {
  .post-control {
    justify-content: space-around;
  }
  .post-content {
    font-size: 1rem;
  }
}
</style>



