<template>
<div class="community section">
  <div class="container">
    <div class="content">
      <div class="wrapper">
        <div class="filters">
          <button @click="sortByFavoritesDescending" :class="{'active': sort.descending}">Top</button>
          <button @click="sortByFavoritesAscending" :class="{'active': sort.ascending}">Bottom</button>
          <button @click="sortByDateRecentFirst" :class="{'active': sort.recent}">Recent</button>
          <button @click="sortByDateOldestFirst" :class="{'active': sort.oldest}">Oldest</button>
        </div>
        <div class="post-list">
          <Post v-for="post in posts" :key="post.id" :post="post"></Post>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import {
  mapGetters,
  mapActions
} from 'vuex';
import Post from './Post';

export default {
  name: 'Community',
  components: {
    Post
  },
  data() {
    return {
      sort: {
        descending: false,
        ascending: false,
        recent: false,
        oldest: false
      }
    }
  },
  created() {
    this.$store.dispatch('getAllPosts');
  },
  methods: {
    sortByFavoritesDescending() {
      this.clearSortStyles();
      this.sort.descending = true;
      this.posts.sort((a, b) => {
        return b.favorites - a.favorites;
      });
    },
    sortByFavoritesAscending() {
      this.clearSortStyles();
      this.sort.ascending = true;
      this.posts.sort((a, b) => {
        return a.favorites - b.favorites;
      });
    },
    sortByDateRecentFirst() {
      this.clearSortStyles();
      this.sort.recent = true;
      this.posts.sort((a, b) => {
        return (Date.parse(b.date)) - (Date.parse(a.date));
      });
    },
    sortByDateOldestFirst() {
      this.clearSortStyles();
      this.sort.oldest = true;
      this.posts.sort((a, b) => {
        return (Date.parse(a.date)) - (Date.parse(b.date));
      });
    },
    clearSortStyles() {
      for (let key in this.sort) {
        this.sort[key] = false;
      }
    }
  },
  computed: {
    ...mapGetters({
      posts: 'allPosts'
    })
  }
}
</script>
<style scoped>
h1 {
  color: #555555;
  letter-spacing: 0.1rem;
  text-align: center;
  display: inline-block;
  margin: 0 auto;
  font-size: 1.55rem;
  padding-bottom: 0.15rem;
  border-bottom: 2px solid #1de0c3;
  margin-bottom: 8px;
  font-weight: bold;
}

.section {
  padding-top: 0.75rem;
}

.filters {
  padding: 0.75rem 0 0.25rem 0;
  margin-bottom: 1rem;
  border-top: 2px solid #1de0c3;
  border-bottom: 2px solid #1de0c3;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.filters button {
  border: none;
  padding: 0 0.875em;
  align-items: center;
  border-radius: 290486px;
  color: #4a4a4a;
  display: inline-flex;
  font-size: 0.75rem;
  height: 2em;
  justify-content: center;
  line-height: 1.5;
  white-space: nowrap;
  margin-bottom: 0.5rem;
}

.filters button.active {
  background-color: #363636;
  color: whitesmoke;
}

.filters button:not(:last-child) {
  margin-right: 14px;
}

.post-list {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: 1fr;
  grid-row-gap: 1rem;
}

@media(min-width: 769px) {
  h1 {
    font-size: 2.5rem;
    padding: 0 2rem 0.15rem 2rem;
  }
  .filters {
    justify-content: flex-start;
  }
}
</style>



