import shop from '../../api/products';
import * as types from '../mutationTypes';

// initial state
const state = {
  all: []
}

// getters
const getters = {
  allProducts: state => state.all
}

// actions
const actions = {
  getAllProducts({ commit }) {
    shop.getProducts(products => {
      commit(types.RECEIVE_PRODUCTS, { products })
    })
  }
}

// mutations
const mutations = {
  [types.RECEIVE_PRODUCTS](state, { products }) {
    state.all = products
  },
  [types.INCREMENT_PRODUCT_CART_QUANTITY](state, { id }) {
    const record = state.all.find(p => p.id === id);
    if (record && record.inventory > 0) {
      record.inventory--;
    }
  },
  [types.DECREMENT_PRODUCT_CART_QUANTITY](state, { id }) {
    const record = state.all.find(p => p.id === id);
    if (record) {
      record.inventory++;
    }
  },
  [types.REMOVE_PRODUCT_FROM_CART](state, { id, quantity }) {
    state.all.find(p => p.id === id).inventory += quantity;
  },
  [types.ADD_TO_CART](state, { id }) {
    state.all.find(p => p.id === id).inventory--
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



