<template>
<div class="shop content">
  <div class="section product-list">
    <div class="container">
      <div class="filter-container">
        <button @click="sortByPrice">
          <icon v-if="sort.price.directionHighLow" name="chevron-down"></icon>
          <icon v-else name="chevron-up"></icon>
          Sort By Price
        </button>
      </div>
      <div class="product-grid">
        <ShopItem v-for="product in products" :key="product.id" :product="product" v-if="product.inventory"></ShopItem>
      </div>
    </div>
  </div>
</div>
</template>
<script>
import ShopItem from './ShopItem';

import {
  mapGetters
} from 'vuex';
export default {
  name: 'Shop',
  components: {
    ShopItem
  },
  data() {
    return {
      sort: {
        price: {
          directionHighLow: false
        }
      }
    }
  },
  created() {
    this.$store.dispatch('getAllProducts');
  },
  computed: {
    ...mapGetters({
      products: 'allProducts'
    })
  },
  methods: {
    sortByPrice() {
      this.products = this.products.sort((a, b) => {
        if (this.sort.price.directionHighLow) {
          return a.price - b.price;
        } else {
          return b.price - a.price;
        }
      });
      this.sort.price.directionHighLow = !this.sort.price.directionHighLow;
    }
  }
}
</script>
<style scoped>
.filter-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding: 1rem;
}

.fa-icon {
  width: auto;
  height: 11px;
  margin-right: 4px;
}

.product-list {
  padding: 0 1rem;
}

.product-grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-auto-rows: 2rem 1fr repeat(3, auto);
  grid-gap: 2rem;
  margin-bottom: 2rem;
}

@media(min-width: 600px) {
  .product-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media(min-width: 700px) {
  .product-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media(min-width: 900px) {
  .product-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media(min-width: 1024px) {
  .product-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
</style>



