<template>
<transition name="shop-item-fade">
  <div class="product-grid-item" v-if="product.active">
    <div class="product-img image">
      <img :src="product.image" />
    </div>
    <div class="product-details">
      <div class="product-description">
        <h2>{{product.name}}</h2>
        <p>{{product.description}}</p>
      </div>
      <div class="product-footer">
        <div class="product-price">
          <b>${{product.price}}</b></small>
        </div>
        <div class="add-to-cart-button">
          <button @click="addToCart(product)">Add To Cart</button>
        </div>
        <div class="product-inventory">
          <small><b>{{product.inventory}}</b><br />Remaining</small>
        </div>
      </div>
    </div>
  </div>
</transition>
</template>
<script>
import {
  mapActions
} from 'vuex';
export default {
  name: 'ShopItem',
  props: ['product'],
  methods: {
    removeComma(color) {
      let comma_index = color.split('').indexOf(',');
      if (comma_index >= 0) {
        return color.substr(0, comma_index);
      } else {
        return color;
      }
    },
    addProductToCart(p) {
      this.addToCart(p);
    },
    ...mapActions(['addToCart'])
  }
}
</script>
<style scoped>
.product-grid-item {
  background-color: #f6f6f6;
  border-top: 8px solid var(--main-color);
  box-shadow: 0 1px 1px 1px rgba(10, 10, 10, 0.2);
}

.product-details {
  margin: 0.5rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.product-description {
  flex: 1 0 125px;
}

.product-footer {
  margin-top: 2rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.product-inventory {
  font-size: 0.8rem;
  line-height: 80%;
}

.product-footer>div {
  flex: 1 0 0px;
}

.add-to-cart-button button {
  padding: 0 0.5rem;
  height: 1.55rem;
  line-height: 1.55rem;
  font-size: 75%;
  align-self: flex-end;
  letter-spacing: 0px;
}

.shop-item-fade-enter-active,
.shop-item-fade-leave-active {
  transition: opacity .3s ease;
}

.shop-item-fade-enter,
.shop-item-fade-leave-to {
  opacity: 0;
}
</style>



