import posts from '../../api/community';
import * as types from '../mutationTypes';

// initial state
const state = {
  all: []
}

// getters
const getters = {
  allPosts: state => state.all
}

// actions
const actions = {
  getAllPosts({ commit }) {
    posts.getPosts(posts => {
      commit(types.RECEIVE_POSTS, { posts })
    })
  }
}

// mutations
const mutations = {
  [types.RECEIVE_POSTS](state, { posts }) {
    state.all = posts
  },
  [types.INCREMENT_POST_FAVORITE_COUNT](state, { post }) {
    const record = state.all.find(p => p.id === post.id);
    if (record) {
      record.favorites++;
    }
  },
  [types.DECREMENT_POST_FAVORITE_COUNT](state, { post }) {
    const record = state.all.find(p => p.id === post.id);
    if (record && record.favorites > 0) {
      record.favorites--;
    }
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



